  padding: 0;
}

.App {
  height: 100vh;
  background: #f8f8f8;
  font-family: "Questrial", sans-serif;
  overflow-x: hidden;
}

nav #reseaux img {
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

nav #reseaux img:hover {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

#contain {
  border-bottom: 2px solid black;
  border-top: 2px solid black;
  width: 75%;
  margin: 45px auto 15px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

#contain::-webkit-scrollbar {
  width: 5px;
}

#contain::-webkit-scrollbar-track {
  background: transparent;
}

#contain::-webkit-scrollbar-thumb {
  background-color: black;
  border-radius: 20px;
}

#contain #home h1 {
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

#contain #home h1:hover {
  text-shadow: 4px 4px 2px black;
}

#contain #home p {
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border-radius: 30px;
}

#contain #home p:hover {
  text-shadow: 4px 4px 2px black;
}

#home,
#contact,
#contain-projets {
  width: 100%;
  height: 80%;
