}

#contain-competences #competences .svg-container {
  margin: 0 5px;
}

#contain-competences #competences .svg-container svg circle {
  stroke-width: 10px;
  fill: transparent;
  stroke-dasharray: 535px;
  -webkit-filter: drop-shadow(1px 3px 2px rgba(0, 0, 0, 0.7));
          filter: drop-shadow(1px 3px 2px rgba(0, 0, 0, 0.7));
}

@-webkit-keyframes pourcentagesLoad {
  from {
    stroke-dashoffset: 535;
  }
}

@keyframes pourcentagesLoad {
  from {
    stroke-dashoffset: 535;
  }
}

#contain-competences #competences .svg-container svg #circleSvg_html {
  stroke: orange;
}

#contain-competences #competences .svg-container svg #circleSvg_css {
  stroke: skyblue;
}

