  letter-spacing: 0.1em;
}

#contact {
  text-align: center;
}

#contact h2 {
  margin: 5px 0;
}

#contact form {
  position: relative;
  width: 75%;
  height: 98%;
  margin: 0 auto;
  padding: 0 2px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: #f8f8f8;
}

#contact form .error {
  visibility: visible;
  position: initial;
  color: red;
}

#contact form .hide {
  visibility: hidden;
}

.ellipses {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 2px auto;
}

.ellipses svg {
  margin: 0 5px;
}

